import Vue from 'vue'
import Router from 'vue-router'
import home from "../components/page/home/home.vue";
import movie from "../components/page/movie/movie.vue";
import cinema from "../components/page/cinema/cinema.vue";
import nowMovie from '../components/page/movie/nowPlay.vue';
import coming from '../components/page/movie/coming.vue'
import xq from '../components/page/movie/xq.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      redirect:"/home"
    },{
      path:"/home",
      name:"home",
      component:home
    }
    ,{
      path:"/movie",
      name:"movie",
      component:movie,
      children:[
      {
      name:'nowMovie',
      path:'nowMovie',
      component:nowMovie,
      },
      {
        name:'coming',
        path:'coming',
        component:coming
       }, 
       {
      name:'xq',
      path:'xq',
      component:xq,
    }
      ]
    },{
      path:"/cinema",
      name:"cinema",
      component:cinema
    },
   
  ]
})



