import axios from "axios";
export default {
    //https://m.maizuo.com
    getBanner({commit}){
        axios({
            method:"get",
            url:"/v4/api/billboard/home",
            data:{
                __t:new Date().getTime()
            }
        }).then((data)=>{
           commit("getBanner",data.data.data.billboards)
        })
    },
    // https://m.maizuo.com/
    getnowPlay({commit}){
        axios({
            method:"get",
            url:"/v4/api/film/now-playing?page=1&count=5",
            data:{
                __t:new Date().getTime()
            }
        }).then((data)=>{
          // console.log(data.data.data.films)
           commit("getnowPlay",data.data.data.films)
        })
    },
    getUpMovie({commit}){
        axios({
            method:'get',
            url:'/v4/api/film/coming-soon?page=1&count=3',
            data:{
               __t: new Date().getTime()
            }
        }).then((data)=>{
            var data=data.data.data.films
            data.map(function(item){
                var getTime=item.premiereAt;
                var d=new Date(getTime)
                var m=d.getMonth()+1
                var day=d.getDate()
                item.date=m+'月'+day+'日上映'
            })
         
            commit('getUpMovie',data)
        })
    },
    nowPlayMore({commit}){
        // https://m.maizuo.com/v4/api/film/now-playing?page=1&count=7
        axios({
            method:"get",
            url:"/v4/api/film/now-playing?page=1&count=7"
        }).then((data)=>{
          
            commit("nowPlayMore",data.data.data.films)
           
        })
    },
    updateMovie({commit},page){
            axios({
                method:'get',
                url:'/v4/api/film/now-playing?page='+page+'&count=7',
            }).then((data)=>{
                commit("nowPlayMore",data.data.data.films)
            })
    },
    xqMovie({commit},id){
        axios({
            method:'get',
            //https://m.maizuo.com/v4/api/film/4436?__t=1540285493328
            url:'/v4/api/film/'+id,
            // data:{
            //     __t:new Date().getTime()
            // }
        }).then((data)=>{
            console.log(data.data.data.film)
            commit('xqMovie',data.data.data.film)
        })
    }
}


