<template>
    <div>
        <div class='xqw'>
            <p>{{xq.name}}</p>
            <p><img :src="xq.cover.origin" ></p>
        </div>
    </div>
</template>

<script>
import Vuex from 'vuex' 
export default {
  
    data(){
        return{
            id:''
        }
    },
    created(){
        this.id=this.$route.query.id;
        this.$store.dispatch('movie/xqMovie',this.id);
        console.log(this.id)
        console.log(this.xq.name)
    },
    methods:{
        ...Vuex.mapActions({
            xqMovie:'movie/xqMovie'
        })
    },
    computed:{
        ...Vuex.mapState({
            xq:state=>state.movie.xqmovie
            
        })
      
    }
}

</script>
<style>
.xqw{
    width:100%;
    height: 100%;
    z-index:999;
    margin-top:100px
}
img{
    width:100%
}

</style>



