<template>
    <div class="films_movie page">
        <ul class='fixed'>
            <li>
               <router-link :to='{name:"nowMovie"}'>正在热映</router-link>
            </li>
            <li>
                <router-link :to='{name:"coming"}'>即将热映</router-link>
            </li>
        </ul>
        <router-view></router-view>
    </div>    
</template>
<script>
import Vuex from 'vuex'
export default {
    created(){
        this.nowPlayMore()
        this.$router.push({name:'nowMovie'})
    },
    methods:{
        ...Vuex.mapActions({
            nowPlayMore:'movie/nowPlayMore'
        })
    },
   
}
        
    

</script>
<style scoped>
.films_movie{
    padding:1rem .3rem 0;
    background:#fff;
    
}
.films_movie>ul{
    display: flex;
    justify-content: space-around;
    font-size:.32rem;
    width:100%;
    height: .9rem;
    background: #fff;
    border-bottom:2px solid #fe6e00;
   z-index:11
}
.films_movie>ul>li{
    width: 50%;
    height: 100%;
}
.films_movie>ul>li>a{
    padding-left:35%;
    color:#6a6a6a;
    line-height: .9rem;
    display: block;
    width:100%;
    height: 100%;
}
.films_movie .router-link-active{
    border-bottom:3px solid  #fe6e00
}
.fixed{
    position: fixed;
}

</style>



