<template>
    <div class="playing wrapper" ref="wrapper">
        <ul class="content">
            <li v-for="(item,index) in movies" @click='handleGo(item.id)'>
                <div>
                    <img :src="item.poster.origin">
                    <div class="desc">
                        <p>{{item.name}}</p>
                        <p>{{item.intro}}</p>
                        <p>
                        <span>{{item.cinemaCount}}家影院上映</span>
                        <span>{{item.watchCount}}人购票</span>
                        </p>
                    </div>
                </div>
                <div>{{item.grade}}<i>></i></div>
            </li>
            
        </ul>
        <router-view></router-view>
    </div>
</template>

<script>
import Vuex from 'vuex';
import BScroll from 'better-scroll';
export default {
    data(){
        return {
            page:2,
            timer:null
        }
    },
    computed:{
        ...Vuex.mapState({
            movies:state=>state.movie.movieMore
        })
    },
    methods:{
        ...Vuex.mapActions({
            updateMovie:"movie/updateMovie"
        }),
        handleGo(id){
            this.$router.push({path:'xq',query:{id}})
        }
    },
    mounted() {

        this.scroll = new BScroll(this.$refs.wrapper,{
            click:true,
        });
        
    },
    updated() {
       
        //当延迟100毫秒以后重新更新DOM的高度
        setTimeout(()=>{
            this.scroll.refresh();
        },100)
        //当用户滚动结束的时候 判断一个当前的页数  以及请求的状态
        this.scroll.on("scrollEnd",()=>{
            if( this.page<7 && !this.timer){
                //延迟3秒以后进行数据的请求
              this.timer = setTimeout(()=>{
                  //将当前页数进行++
                    this.updateMovie(this.page++);
                    //当请求结束的时候将状态值改为空
                     this.timer = null
               },1500)
              
            }
        })
    },
}

/*
    1、下载better-scroll  cnpm install better-scroll --save-dev

    2、引入better-scroll  import BScroll from "better-scroll";

    3、在需要滚动的盒子上加class content 父级盒子加wrapper
        当前父级盒子（wrapper） 必须要有高度和<overflow:hidden>
        
        
        
    4、创建scroll  (注意在能获取到DOM结构的生命周期中获取)
        this.scroll = new BScroll(需要滚动的DOM结果);
    
    5、当数据发生改变得时候调用this.scroll.refresh方法进行高度的重新获取


    6、上拉刷新更新数据
        this.scroll.on("scrollEnd",()=>{
            //更新数据
        })


 */
</script>

<style scoped>
.wrapper{
    height:100%;
    overflow:hidden;
    margin-top:.5rem
}
.playing{
    width:100%;
    padding-right:30px;
}
.playing>ul>li{
    height:2.48rem;
    padding:40px 0;
    display:flex;
    justify-content:space-between;
    border-bottom:2px dashed #ccc;
}
.playing>ul>li>div:nth-child(1){
    display:flex;
    justify-content:center;

}
img{
    width:1.2rem;
    height:1.68rem;
}
</style>


