<template>
    <div class="mask" @click='xxx'></div>
</template>

<script>
export default {
    data(){
        return{

        }
    },
    methods:{
        xxx(){
            this.$emit('handleshow',false)
        }
    }
}
</script>

<style>
.mask{
    width:100%;
    height:100%;
    background:rgba(0,0,0,.5);
    position:fixed;
    left:0;
    top:1rem;
    z-index:101
}


.fade-enter-active{
    animation:fadeEnter 300ms;
}

.fade-leave-active{
    animation:fadeLeave 300ms;
}


@keyframes fadeEnter{
    0%{
        opacity:0
    }
    100%{
        opacity:.5
    }
}


@keyframes fadeLeave{
    0%{
        opacity:.5
    }
    100%{
        opacity:0
    }
}
</style>


